.inputfield{
    margin-bottom:20px;
    display:flex;
    flex-direction:row;
}
.inputfield input{
    height: 30px;
    width: 100%;
    font-size: 18px;
    padding: 6px;
}
.inputfield button{
    margin-left:4px;
    height:45px;
    width:50px;
    background-color: #9049eb;
    color:#fff;
    border: 0;
    border-radius:2px;
    font-size:40px;
    font-weight:600;
}