.todoList{
    max-height:500px;
    overflow-y: auto;
}

.list span{
    margin-left:10px;
    word-wrap: break-word;
    width: 90%;
}

.completed.list{
    text-decoration: line-through;
}

.todoList h4{
    color:#000;
    margin-top:0px;
}

.list{
    background-color: #f3f1f3;
    color:#000;
    font-size:16px;
    padding:10px;
    line-height:25px;
    border-radius:5px;
    display:flex;
    flex-direction:row;
    margin-bottom:10px;
  
}
  
.list span{
    margin-left:10px;
    word-wrap: break-word;
    width: 90%;
}